.site-list {
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
  background-color: $primary-color;
  list-style: none;
}

.site-list a {
  padding: 20px 25px;
  font-size: $font-size-medium;
  line-height: $line-height-nav;
  font-weight: 600;
  text-transform: uppercase;
  text-decoration: none;
  color: $second-color;
}

.site-list__item {
  padding: 20px 0;
  color: $second-color;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000001;
}

/*.site-list__item a {

} */

.site-list a[href] {
  &:hover {
    color: $highlight-color;
  }

  &:focus {
    color: $highlight-color;
  }

  &:active {
    color: $second-color-active;
  }
}

@media (min-width: $desktop-width) {
  .site-list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    background-color: transparent;
  }

  .site-list a {
    margin: 0;
    padding: 0;
    font-weight: 400;
    text-transform: none;
  }

  .site-list__item {
    position: relative;
    padding: 0;
    border: none;

    &--active::before {
      content: "";
      position: absolute;
      left: 0;
      top: 31px;
      height: 2px;
      width: 100%;
      display: block;
      background-color: $second-color;
    }
  }
}
